'use client'
import React from 'react';

import { useRouter } from 'next/navigation'
import { useJob } from '../context/context';
type Posting = {
  id:number;
  title: string;
  description: string;
  responsibilities: string[];
  ideal_candidate: {
    age: string;
    gender: string;
    traits: string[];
  };
  when_where: string;
  about: {
    posted_on: Date;
    deadline: Date;
    location: string;
    start_date: string;
    end_date: string;
    categories: string[];
    required_skills: string[];
  };
  company: string;
  image: string;
};

type Props = {
  job: Posting;
};

function CardComponent({ job }: Props,) {
    const router=useRouter()
    const {setSelectedJob}=useJob()
    const handleClick=()=>{
        setSelectedJob(job)
        router.push('/description');
    }
  return (
 
  <div className="block p-4 border rounded hover:bg-gray-100" onClick={handleClick}>
    <h2>{job.title}</h2>
    <h4>
      {job.company}{' '}
      <ul className="list-disc p-4 m-0 inline-block">
        <li>{job.about.location} {job.id}</li>
      </ul>
    </h4>
    <p>{job.description}</p>
    <p>
      Posted on:{' '}
      {new Date(job.about.posted_on).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      })}
    </p>
  </div>


  );
}

export default CardComponent;
